import request from "./request"

// 登陆
export const Login = (data) => request("post", "/login", data);

// 验证用户是否登陆
export const CheckLogin = () => request("get", "/checklogin");

// 注销
export const Cancellation = () => request("post", "/cancellation")

// 上传头像
export const ImageUpload = (data, options) => request("post", "/image/upload", data, options)

// 获取用户信息
export const FindFile = () => request("get", "/findfile")

// 修改用户信息
export const Modify = (data) => request("pose", "/modifyflie", data)

// 发表文章
export const PublishedArticle = (data) => request("post", "/article/published", data)

// 根据条件获取文章(data不传则获取所有)
export const GetArticle = (data) => request("get", "/article/get", data)

// 根据条件搜索文章
export const SearchArticle = (data) => request("post", "/article/search", data)

// 获取单页的文章
export const GetSinglePage = (data) => request("get", "/article/singlepage", data)

// 删除文章
export const DeleteArticle = (data) => request("post", "/article/delete", data)

// 发布公告
export const ReleaseAnnount = (data) => request("post", "/annount/release", data)

// 获取公告
export const GetAnnount = (data) => request("get", "/annount/get", data)

// 删除公告
export const DeleteAnnount = (data) => request("post", "/annount/delete", data)

// 发布留言
export const PostMessage = (data) => request("post", "/message/post", data)

// 获取留言
export const GetMessage = (data) => request("get", "/message/get", data)

// 删除留言
export const DeleteMessage = (data) => request("post", "/message/delete", data)


