<template>
  <div>
    <div class="userphoto">
      <img :src="imageUrl" alt />
    </div>
    <div class="title">
      <h4>{{userName}}</h4>
      <h5>{{signature}}</h5>
    </div>
    <div>
      <ul>
        <li>
          <router-link to="/home" tag="span">主页</router-link>
        </li>
        <li>
          <span @click="openAnount">公告</span>
        </li>
        <li>
          <router-link to="/message" tag="span">留言</router-link>
        </li>
        <li>
          <span @click="openArchive">归档</span>
        </li>
      </ul>
      <div class="outerlink">
        <a href="https://me.csdn.net/qq_37970097">
          <img src="../assets/csdn.png" alt />
        </a>
        <a href="https://gitee.com/ymshen" class="amiddle">
          <img src="../assets/mayun.png" alt  />
        </a>
        <a href="https://github.com/lijinshen-ym?tab=repositories">
          <img src="../assets/github.png" alt/>
        </a>
      </div>
    </div>
     <div class="beian">备案号：粤ICP备19141395号</div>
  </div>
</template>
<script>
import { FindFile } from "../api/index";
export default {
  name: "Left",
  data() {
    return {
      imageUrl: "",
      userName: "",
      signature: ""
    };
  },
  created() {
    this.getFile();
  },
  mounted() {
    this.$EventBus.$on("updateUser", this.getFile);
  },
  methods: {
    openAnount() {
      this.$emit("changeDrawer", { type: "annount" });
    },
    openArchive() {
      this.$emit("changeDrawer", { type: "archive" });
    },
    getFile() {
      FindFile().then(res => {
        this.userName = res.userName;
        this.signature = res.signature;
        this.imageUrl = res.photo;
      });
    }
  }
};
</script>
<style lang="scss" scoped>
div {
  text-align: center;
  position: relative;
}

.userphoto {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  overflow: hidden;
  margin-left: 45px !important;
  background: wheat;
  img {
    display: inline-block;
    object-fit: cover;
    height: 120px;
    width: 120px;
    border-radius: 50%;
  }
}

.title {
  margin: 20px 0px;
  h4 {
    font-size: 30px;
    color: rgb(122, 124, 124) !important;
    margin: 20px 0px;
  }
  h5 {
    color: rgb(134, 133, 133) !important;
    font-weight: normal;
    font-size: 16px;
    margin: 40px 0px;
  }
}
ul li {
  list-style: none;
  margin: 20px 0px;
  span {
    color: rgb(122, 119, 119);
  }
  span:hover {
    cursor: pointer;
    opacity: 0.7;
  }
}
.outerlink {
  margin-top: 40px;
  text-align: center;
  .amiddle {
    margin: 0px 15px;
  }
  a {
    text-decoration: none;
    color: white;
    display: inline-block;

    vertical-align: middle;
    img {
      width: 25px;
      vertical-align: middle;
    }
  }
}
.beian {
