<template>
  <el-container id="app">
    <el-container>
      <el-aside width="25%">
        <left ref="left" @changeDrawer="changeDrawer"></left>
       
      </el-aside>
      <el-container>
        <el-header>
          <div class="hleft">
            <header-search></header-search>
          </div>
          <div class="hright">
            <el-button type="primary" @click="handleLogin">后台管理</el-button>
            <el-button type="primary" @click="handleCancellation" v-if="isLogin">退出</el-button>
          </div>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
    <el-drawer :visible.sync="drawer" :before-close="handleClose" :with-header="false">
      <vue-scroll :ops="ops">
        <div class="timelinebox">
          <h2>{{drawertitle}}</h2>
          <el-timeline>
            <el-timeline-item
              v-for="(item,index) in annount"
              :key="index"
              :timestamp="item.date"
              placement="top"
              type="primary "
            >
              <el-card>
                <p>{{item.title?item.title:item.content}}</p>
              </el-card>
            </el-timeline-item>
          </el-timeline>
        </div>
      </vue-scroll>
    </el-drawer>
  </el-container>
</template>

<script>
import Left from "./views/Left";
import HeaderSearch from "./views/HeaderSearch";
import { GetAnnount, CheckLogin, Cancellation, GetArticle } from "./api/index";
export default {
  name: "App",
  data() {
    return {
      isLogin: false,
      drawertitle: "",
      flag: true,
      drawer: false,
      annount: [], //储存公告的数组
      count: 0, //公告的数量
      ops: {
        //vuescroll的配置
        vuescroll: {
          sizeStrategy: "percent",
          detectResize: true
        },
        scrollPanel: {},
        rail: {},
        bar: {
          showDelay: 500,
          onlyShowBarOnScroll: false,
          keepShow: false,
          background: "#b3c0d1",
          opacity: 1,
          hoverStyle: false,
          specifyBorderRadius: false,
          minSize: false,
          size: "6px",
          disable: false
        }
      }
    };
  },
  created() {
    CheckLogin().then(res => {
      this.isLogin = res.isLogin;
    });
  },
  mounted() {
    // 事件总线，监听登录状态，$emit 位于Management.vue组件中
    this.$EventBus.$on("changeIsLogin", () => {
      this.isLogin = true;
    });
  },
  components: {
    Left,
    HeaderSearch
  },
  methods: {
    handleClose(done) {
      done();
    },
    // 打开公告栏
    changeDrawer(args) {
      this.drawer = true;
      if (args.type == "annount") {
        this.drawertitle = "公告";
        this.getAnnount({});
      } else {
        this.drawertitle = "归档";
        this.getartile();
      }
    },
    // 获取公告
    getAnnount(data) {
      GetAnnount(data).then(res => {
        this.annount = [];
        const { data, status, count } = res;
        if (status) {
          this.annount.push(...data);
          this.count = count;
        }
      });
    },
    getartile(data) {
      GetArticle(data).then(res => {
        this.annount = [];
        const { data, status, count } = res;
        if (status) {
          this.annount.push(...data);
          this.count = count;
        }
      });
    },
    // 后台管理
    handleLogin() {
      if (this.isLogin) {
        this.$router.push("/backend");
      } else {
        this.$router.push("/login");
      }
    },
    // 注销
    handleCancellation() {
      this.$confirm("您确定退出登录", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Cancellation().then(res => {
            const { status, isLogin, msg } = res;
            if (status) {
              this.$notify({
                title: "成功",
                message: msg,
                type: "success"
              });
              this.isLogin = isLogin;
              console.log(this.isLogin);
              this.$router.push("/home");
            }
          });
        })
        .catch(() => {});
    }
  }
};
</script>

