import Vue from 'vue'
import {
    Container,
    Header,
    Aside,
    Main,
    Footer,
    Form,
    FormItem,
    Input,
    Button,
    Row,
    Message,
    Tabs,
    TabPane,
    Table,
    TableColumn,
    DatePicker,
    Upload,
    Col,
    Image,
    Dialog,
    MessageBox,
    Card,
    Tag,
    Notification,
    Tooltip,
    Pagination,
    Select,
    Option,
    Drawer,
    Timeline,
    TimelineItem,
    Badge
} from 'element-ui';

const element = {
    install: function (Vue) {
        Vue.use(Container);
        Vue.use(Header);
        Vue.use(Aside);
        Vue.use(Main);
        Vue.use(Footer);
        Vue.use(Form);
        Vue.use(FormItem);
        Vue.use(Input);
        Vue.use(Button);
        Vue.use(Row);
        Vue.use(Tabs);
        Vue.use(TabPane);
        Vue.use(Table);
        Vue.use(TableColumn);
        Vue.use(DatePicker);
        Vue.use(Upload);
        Vue.use(Col);
        Vue.use(Image);
        Vue.use(Dialog);
        Vue.use(Card);
        Vue.use(Tag);
        Vue.use(Tooltip);
        Vue.use(Pagination);
        Vue.use(Select);
        Vue.use(Option);
        Vue.use(Drawer);
        Vue.use(Timeline);
        Vue.use(TimelineItem)
        Vue.use(Badge)

        Vue.prototype.$message = Message;
        Vue.prototype.$confirm = MessageBox.confirm;
        Vue.prototype.$notify = Notification;
    }
}
export default element